@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@500&display=swap');

html{
  background: #04897c;
}

.timer-title{
  text-align: center;
  color: white;
  font-family:"Poppins", sans-serif;
}
#timer-label{
  font-size: 40px;
}

#timer-sublabel{
  font-size: 28px;
}

#session-label{
  font-size: 20px;
  font-family:"Poppins", sans-serif;
  margin-bottom: 3%;
}

div #pomodoro-timer{
  background-color: #dddddd;
  position: relative;
  padding: 3%;
  margin-left: 10%;
  margin-right: 10%;
  padding-right: 15%;
  padding-left: 15%;
}

#subtimer{
  width: 350px;
  text-align: center;
  padding-bottom: 6%;
}

#clock{
  text-align: center;
}

#clock-label{
  text-align: center;
  font-family:"Poppins", sans-serif;
  font-size: 32px;
  padding-bottom: 0;
  margin-bottom: 6%;
}

#sublabel-line{
  width: 60%;
}

#session-timer{
  font-size: 24px;
  padding-top: 15px;
  height: 45px;
  width: 100px;
  background-color: #111111;
  color: #cc0000;
  font-family: 'Orbitron', sans-serif;
}

#clock-timer{
  font-size: 32px;
  padding-top: 20px;
  height: 60px;
  width: 200px;
  background-color: #111111;
  color: #cc0000;
  font-family: 'Orbitron', sans-serif;
}

button{
  width: 40%;
  height: 50px;
  font-family:"Poppins", sans-serif;
  font-size: 14px;
}

button:hover{
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}

#incrementBtn{
  background-color: #4fa866;
}

#decrementBtn{
  background-color: #ae4141;
}

#playBtn{
  background-color: #3d85c6;
}

#resetBtn{
  background-color: #e69138
}

